.header {
  /* background-color: rgb(82, 167, 182); */
  background-image: url('https://www.axiam.co.za/wp-content/uploads/2017/07/disney-header.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  padding: 180px;
  font-family: waltDisney;
  font-size: 30px;
  color: aliceblue;
}

.categories {
  /* background-color: rgb(160, 160, 160); */
  color: aliceblue;
  font-size: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  text-transform: uppercase;
}

.float-left {
  float: left;
  display: -webkit-flex;
  display: flex;
}

p {
  margin: 15px;
}

.float-right {
  display: -webkit-flex;
  display: flex;
  margin-top: 12px;
  -webkit-flex-direction: column;
          flex-direction: column;
}

input {
  border-radius: 5px;
  height: 20px;
}

.filters {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: red;
}

.filter{
  margin: 20px;
  border: 2px red solid;

  padding: 20px;
  font-size: 18px;
  border-radius: 50%;
}

.list {
  display: -webkit-flex;
  display: flex;
  max-width: 10px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.row{
  display: -webkit-flex;
  display: flex;
  margin-left: 300px;
  /* justify-content: center; */
  /* align-content: center; */
}

.album-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.film {
  margin: 10px;
  color: white;
}

.inactive {
  display: none;
}

.is-active {
  display: inline;
}

.details {
  color: white;
}

.clear {
  clear: both;
}

.detailsCard {
  margin-left: 130px;
}

.detailsCard .float-left {
  float: left;
  margin-right: 10px;
}

.detailsCard .float-right {
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 500px;
  /* padding-top: 50px; */
  text-align: left;
}

.video {
  margin-right: 100px;
  margin-top: 30px;
}

.album {
  color: white;
}
